footer {
    max-width: 1200px;
    height: 5rem;
    margin: 0 auto;
    .footer-wrapper {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: center;
        padding-top: 2em;
        margin: 0 auto;
        background-color: $color-primary;
        color: $color-light;
        text-align: center;
    }
    a {
        color: $color-light;
    }
}

.rs img {
    width: 25%;
}
